<template>
	<div class="saleedi-report-form-order">
		<div class="report-title">
			<div class="order-ent">{{$route.query && $route.query.companyName}}</div>
		</div>
		<div class="report-form-warp">
			<w-table
				:tableData="reportOrderData"
				class="report-table"
				show-summary
				:summary-method="getSum"
				@rowClick="rowClickHandle">
				<w-table-column prop="companyName" label="订货机构" align="left"></w-table-column>
				<w-table-column prop="orderBillAmount" label="订单数量"></w-table-column>
				<w-table-column prop="orderSumMoney" label="订货总金额" align="right"></w-table-column>
				<w-table-column prop="deliverySumMoney" label="发货总金额" align="right"></w-table-column>
			</w-table>
		</div>
	</div>
</template>

<script>
const { reportSumAddUnitFunc } = require('@/js/lib/common')
export default {
	name: 'reportFormOrder',
	data () {
		return {
			$query: this.$route.query,
			reportOrderData: []
		}
	},
	created () {
		this.searchHandler()
	},
	methods: {
		getSum (param) {
			return reportSumAddUnitFunc(param, ['orderSumMoney', 'deliverySumMoney'])
		},
		searchHandler () {
			const params = this.$route.query
			params.jump = '1'
			this.$fxHttp.requestPost({
				url: '/sum4BillByMap',
				data: params
			}).then(res => {
				this.reportOrderData = res
			})
		},
		rowClickHandle ([item, index]) {
			this.$router.push({
				path: '/reportFormRdc',
				query: {
					organName: item.organName,
					companyName: this.$route.query.companyName,
					beginDate: this.$route.query.beginDate,
					endDate: this.$route.query.endDate
				}
			})
		}
	}
}
</script>
